html, body {
  margin: 0;
  max-width: 100%;
  overflow: hidden;
}

.app {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: white;
  user-select: none;
  text-shadow: 0 0 1rem rgba(0, 0, 0, 0.4);
}

.bg-image {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.5)), url('https://source.unsplash.com/featured/1600x900/?nature');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

select {
  /*text-indent: 40%; SHIFT STUFF OVER*/
}

.name-header {
  position: absolute;
  top: 0;
  left: 0;
}

.form-control {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.center-exit {
  position: absolute;
  right: 0; left: 0;
  width: 33px;
  line-height: 60px;
}

::-webkit-scrollbar {
  width: 0px;
}

.hover-div {
  width: 75px;
  height: 100%;
  position: absolute;
  margin: auto;
  top:0;bottom:0;
  right:0;
}

.movement-div {
  position: fixed;

  max-width: 350px;
  width: 85vw;
  height: 100%;

  margin: auto;
  top:0;bottom:0;
  right: 0;
  transform: translateX(100%);

  transition: transform 700ms ease-in-out;
}

.hover-div:hover .movement-div {
  transition: transform 400ms ease-in-out;
  transform: translateX(0%);
}

.schedule-panel {
  max-width: 350px;
  width: 85vw;
  height: 100%;

  position: absolute;
  margin: auto;
  top:0;bottom:0;

  background-color: rgba(255, 255, 255, 0.3);
  -webkit-backdrop-filter: blur(15px);
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;

  overflow: scroll;

  padding: 10px;
  padding-top: 8px;
}

@media only screen and (max-width: 600px) {
  .schedule-panel {
    background-color: #696969;
  }
}

.today-info {
  position: absolute;
  margin: auto;
  bottom:0;
  left:0;
}

.class-badge {
  padding: 2px 5px;
  border-radius: 5px;
}

.assign-badge {
  padding: 3px 4px;
  border-radius: 5px;
}

/* TODO: COLOR FIX FOR MAXIMUM READABILITY AND COMPATIBILITY WITH OTHER COLORS */
.red { background: #F46D6D; }
.blue { background: #5585B5; }
.green { background: #0B8457; }
.orange { background: #F59D2A; }
.purple { background: #6A3CBC; }
.yellow { background: #E8BA5F; }
.brown { background: #916131; }
.gray { background: #818D97; }
.dark { background: #545454; }
.lightblue { background: #498EB9; }

.heading-div {
  margin: auto;
  overflow: visible;
}

.non-header {
  display: inline-block;
  width: 100%;
}

.heading-float-left {
  float: left;
}

.heading-float-right {
  float: right;
}

.no-school {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.glyph-icon {
  width:32px;
  height:32px;

  padding: 5px;
  border-radius: 5px;

  margin: auto;
}

.glyph-icon:hover {
  cursor: pointer;
}

.no-margin {
  margin: 0;
}

.month-p {
  padding-bottom: 5px;
}

.upcoming-card-div {
  position: relative;
  display: inline-block;
  overflow: visible;
  width: 100%;
  height: 60px;
  border-radius: 5px;
  padding-right: 7px;
}

.class-card-div {
  position: relative;
  display: inline-block;
  overflow: visible;
  width: 100%;
  height: 60px;
  border-radius: 5px;
  padding-right: 7px;
  box-shadow: 0 0 0.4rem rgba(0, 0, 0, 0.4);
}

.upcoming-div {
  overflow: hidden;
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  box-shadow: 0 0 0.4rem rgba(0, 0, 0, 0.4);

  transition: width 250ms ease-out 50ms;
}

.finish-div {
  overflow: hidden;
  position: absolute;
  right: 0%;
  width: 0;
  height: 100%;
  border-radius: 5px;
  box-shadow: 0 0 0.4rem rgba(0, 0, 0, 0.4);

  transition: width 250ms ease-out 50ms;
}

.upcoming-card-div:hover .upcoming-div {
  width: 88%;
}

.upcoming-card-div:hover .finish-div {
  width: 10%;
}

.card-done {
  position: absolute;
  font-size: 20px;
  text-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.4);
  color: #F1F1F1;
  right: 5px;
}

.text-white {
  color: #F1F1F1;
}

.card-due-class {
  position: absolute;
  font-size: 15px;
  text-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.4);
  color: #F1F1F1;
  line-height: 90px;
  padding-left: 7px;
}

.card-due-date {
  position: absolute;
  font-size: 20px;
  text-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.4);
  color: #F1F1F1;
  right: 0;
  padding-right: 7px;
  padding-bottom: 7px;
  margin-top: 30px;
}

.card-identifier {
  position: absolute;
  font-size: 25px;
  text-shadow: none;
  right: 0;
  color: #afafaf;
  font-variant: small-caps;
  padding-right: 10px;
  padding-top: 10px;
}

.card-name {
  position: absolute;
  font-size: 20px;
  text-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.4);
  color: #F1F1F1;
  padding-left: 7px;
  white-space: nowrap;
}

.card-complete { /* TODO: NICER COLORS FOR THE COMPLETE BUTTON */
  position: absolute;
  width: 100%;
  height: 100%;

  opacity: 0;
  font-size: 25px;
  text-shadow: none;
  color: #4ECCA3;
  font-variant: small-caps;
  font-weight: bold;

  text-align: center;
  line-height: 60px;
}

.exit-button:focus {
  outline: none;
}

.p-b-10 {
  padding-bottom: 10px;
}

.horizontal-rule {
  margin: auto;
  margin-top: 0;
  margin-bottom: 0;
  width: 90%;
  height: 1px;
  border: none;
  border-top: 2px solid;
}

.switch-hr {
  position: relative;
  margin-top: 5px;
  margin-bottom: 5px;
}

.switch-buttons {
  position: absolute;
  width: 100%;
  margin: 0 auto;
  top: -15px;
}
